export default {
	onLoad() {
		var that = this
		if (this.$common.userinfo.openId) {

		} else {
			uni.navigateTo({
				url: '../loginPage/loginPage'
			})
		}
		var postData = uni.getStorageSync("cuotiList")
		uni.request({
			url: 'https://magiskq.top:8081/getCuotiList',
			method: 'POST',
			header: {
				'Content-Type': "application/json"
			},
			data: {
				cuotiList: postData
			},
			success(res) {
				that.$common.cuotiList = res.data
				console.log(res.data)
				that.list = that.$common.cuotiList
				for (var i = 0; i < that.list.length; i++) {
					if (that.zhangjieLit.includes(that.list[i].zhangjie)) {
						var a = that.zhangjieLit.indexOf(that.list[i].zhangjie)
						that.qList[a].qList.push(that.list[i])
					} else {
						that.zhangjieLit.push(that.list[i].zhangjie)
						var a = that.zhangjieLit.indexOf(that.list[i].zhangjie)
						that.qList.push({
							qList: []
						})
						that.qList[a].qList.push(that.list[i])
					}
				}
			}
		})
	},
	onShow() {},
	data() {
		return {
			list: [],
			zhangjieLit: [],
			qList: [],
			cuotiList: [{
					zhangjie: "马克思第一章",
					qList: [{
							content: "马克思注意诞生于（ ）",
							A: '1848年2月',
							B: '1848年1月',
							C: '1843年2月',
							D: '1845年3月',
							tixing: 'danxuan',
							rightchoice: 'A',
							zhangjie: '第一章：asdasdasd',
							zhuti: '马克思主义',
							number: 1,
							jiexi: '111222333444555666'
						},
						{
							content: "马克思主义包含以下哪些内容",
							A: ' 实践是认识的基础',
							B: '认识是主体对客体的能动反映',
							C: '主观能动性大于一切',
							D: '资本主义优于社会主义',
							tixing: 'duoxuan',
							rightchoices: ["A", "B"],
							number: 2,
							jiexi: '111222333444555666'
						},
						{
							content: "卡尔·马克思是马克思主义的创始人之一",
							tixing: 'panduan',
							judgementanswer: 'right',
							number: 3,
							jiexi: '111222333444555666'
						}
					]
				},
				{
					zhangjie: "数电第八章",
					qList: [{}, {}, {}]
				},
				{
					zhangjie: "高数第三章",
					qList: [{}, {}, {}]
				}
			]
		}
	},
	methods: {
		goto(e) {
			var url = `../jiexiPage/jiexiPage?cuotiTag=1`
			this.$common.questionList = this.qList[e].qList
			uni.navigateTo({
				url: url
			})
		}
	}
}
